<template>
  <v-container fluid grid-list-xl>
    <v-dialog v-model="dialog" width="800px">
      <v-card elevation="15" class="special-scroll">
        <v-card-title>Fabriquants de Batteries</v-card-title>
        <v-container>
          <v-form class="mx-5">
            <v-row>
              <v-col cols="12" md="12">
                <v-text-field v-model="editedFabriquant.label" label="fabriquant" required></v-text-field>
              </v-col>
              <!-- <v-col cols="12" md="12">
                <v-text-field v-model="editedFabriquant.description" label="Description"></v-text-field>
              </v-col> -->
            </v-row>
          </v-form>
          <v-row v-show="editedFabriquant.label">
            <v-col cols="12">
              <v-chip light class="ma-2 elevation-5 white--text" color="amber darken-3" @click="save">
                <v-avatar left><v-icon small>mdi-telegram</v-icon></v-avatar>
                <span>Enrégistrer</span>
              </v-chip>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
    <v-layout wrap class="px-0">
      <v-flex sm12 xs12 md12 lg12>
        <v-card tile class="mx-auto mt-8">
          <v-sheet class="v-sheet--offset mx-auto py-3 px-3" color="#0C0C0B" elevation="10" max-width="calc(100% - 32px)">
            <v-card-text class="pt-0">
              <div class="title font-weight-thin mb-0 white--text">
                List of deposits
              </div>
            </v-card-text>
          </v-sheet>

          <v-card-text class="pt-0" v-if="fabriquants">
            <v-data-table :headers="headers" :items="fabriquants" :items-per-page="10" class="mb-3">
              <template v-slot:no-data>
                <p class="dark mt-15">Aucune donnée enrégistrée pour le moment</p>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>

export default {
  data () {
    return {
      currentUser: null,
      editedIndex: -1,
      dialog: false,
      editedFabriquant: {
        id: null,
        accountName: null,
        email: null,
        phone:null,
        date:null,
        userID:null,
        depositAmount:null
      },
      newfabriquant: {
        id: null,
        accountName: null,
        email: null,
        phone:null,
        date:null,
        userID:null,
        depositAmount:null
      },
      fabriquants: [],
      headers: [
        { text: 'Account Name', value: 'accountName' },
        { text: 'Email', value: 'email' },
        { text: 'Phone Number', value: 'phone' },
        { text: 'Deposit Amount', value: 'depositAmount' },
        { text: 'userID', value: 'userID' },
        { text: 'Date', value: 'date' },
        // { text: 'Description', value: 'description' },
      ],
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
  },
  methods: {
    editItem (item) {
      this.editedIndex = this.fabriquants.indexOf(item)
      this.editedFabriquant = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.fabriquants.indexOf(item)
      if (confirm('Êtes vous sûr de vouloir supprimer ?')) {
        this.fabriquants.splice(index, 1)
        this.saveToLocal()
      }
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedFabriquant = Object.assign({}, this.newfabriquant)
        this.editedIndex = -1
      }, 300)
    },
    saveToLocal () {
      let data = JSON.stringify(this.fabriquants)
      window.localStorage.setItem('jules-app-fabriquant-batterie', data)
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.fabriquants[this.editedIndex], this.editedFabriquant)
      } else {
        this.fabriquants.push({
          id: Math.floor(new Date () * Math.random() * Math.floor(9)),
          label: this.editedFabriquant.label,
          description: this.editedFabriquant.description
        })
      }
      this.saveToLocal()
      this.close()
    }
  },
  mounted () {
    this.currentUser = JSON.parse(window.localStorage.getItem('jules-user-connected'))
    if (window.localStorage.getItem('deposits') !== null && window.localStorage.getItem('deposits').length > 0) { this.fabriquants = JSON.parse(window.localStorage.getItem('deposits')) }
  },
}
</script>

<style lang="scss" scoped>
  .v-sheet--offset {
    top: -24px;
    position: relative;
  }
  .card-prepend {
    width: 90%;
    margin-left: 5%;

    .card-offset {
      height: 200px;
    }

    .card-content {
      position: absolute;
      top: 0;
    }
  }
</style>